/* Importing the font from Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Sour+Gummy:ital,wght@0,100..900;1,100..900&display=swap');

/* Global styles */
body, html {
  background: linear-gradient(135deg, #ffb3c6, #fb6f92); /* Soft pink to light peach gradient */
  font-family: 'Sour Gummy', sans-serif; /* Font as per your request */
  color: #264653; /* Soft dark pink for text color */
  margin: 0;
  padding: 0;
  height: 100vh;
}

h1, h2, h3 {
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1); /* Soft shadow for headers */
}

h1 {
  font-size: 36px;
}

h2 {
  font-size: 28px;
  margin-bottom: 20px;
}

h3 {
  font-size: 24px;
}

/* Container styles */
.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 40px;
  background-color: #ffffffa5; /* Semi-transparent white background */
  border-radius: 12px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
  text-align: center;
  opacity: 0;
  animation: fadeIn 1s ease-out forwards;
}

/* Fade-in animation */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Button styles */
button {
  padding: 12px 20px;
  margin: 10px;
  border-radius: 8px;
  border: none;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
  font-family: 'Sour Gummy', sans-serif;
  background-color: #ffb3c6; /* Soft pink color */
  color: #fff;
}

button:hover {
  transform: scale(1.1); /* Slightly bigger button on hover */
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.2); /* Soft shadow around the button */
  background-color: #fb6f92; /* Darker pink on hover */
}

button:focus {
  outline: none;
  box-shadow: 0 0 0 4px rgba(251, 122, 163, 0.5); /* Light pink focus ring */
}

/* Card-style buttons for food and location selections */
button {
  padding: 12px 20px;
  margin: 10px;
  border-radius: 12px;
  
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  font-family: 'Sour Gummy', sans-serif;
  background-color: #fff;
  color: #264653; /* Dark pink text for better visibility */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

button:hover {
  transform: translateY(-5px);
  border: 3px solid #f99eb5;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2);
  background-color: #fb6f92; /* Soft pink hover effect */
  color: white;
}

/* Input fields styles */
input[type="date"], input[type="time"] {
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #f88ab0;
  font-size: 16px;
  margin: 15px 0;
  width: 100%;
  background-color: #f88ab13d; /* Light peach background for inputs */
}

input[type="date"]:focus, input[type="time"]:focus {
  border-color: #f88ab0; /* Focus border with a romantic pink color */
}

/* Reset button (floating button) */
button#reset {
  background-color: #ff4d4d;
  color: #fff;
  padding: 15px 25px;
  border-radius: 50%;
  position: fixed;
  bottom: 20px;
  right: 20px;
  border: none;
  font-size: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease, transform 0.3s ease;
}

button#reset:hover {
  transform: scale(1.1);
  background-color: #fb6f92; /* Darker red when hovered */
}

/* Navigation buttons (Previous & Next) */
.nav-buttons button {
  padding: 10px 18px;
  margin: 5px;
  font-size: 16px;
  border-radius: 8px;
  border: none;
  background-color: #fb6f92; /* Soft pink for navigation buttons */
  color: white;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
}

.nav-buttons button:hover {
  transform: scale(1.1);
  background-color: #fb6f92; /* Darker pink on hover */
}

/* Responsive Design */
@media only screen and (max-width: 600px) {
  .container {
    padding: 15px;
  }

  h1 {
    font-size: 30px;
  }

  h2 {
    font-size: 24px;
  }

  button {
    font-size: 14px;
    padding: 10px 18px;
  }

  input[type="date"], input[type="time"] {
    width: 90%;
  }

  button#reset {
    bottom: 10px;
    right: 10px;
  }
}
